"use client";
import React, { useEffect, useState } from 'react';
import { DragDropContext, Droppable, DropResult } from '@hello-pangea/dnd';
import TaskCard from './TaskCard';
import axios from 'axios';
import { useAuth } from '@/context/AuthContext';
import { io } from 'socket.io-client';
import { Task } from '@/types';

const socket = io('http://localhost:5000');

interface BoardProps {
    refreshTrigger: number;
}

export default function Board({ refreshTrigger }: BoardProps) {
    const { user } = useAuth();
    const [tasks, setTasks] = useState<Task[]>([]);
    const [isMounted, setIsMounted] = useState(false);

    const columns = {
        'todo': 'To Do',
        'in-progress': 'In Progress',
        'done': 'Done'
    };

    const columnColors = {
        'todo': 'bg-gray-50 border-gray-200',
        'in-progress': 'bg-blue-50/50 border-blue-100',
        'done': 'bg-green-50/50 border-green-100'
    }

    const fetchTasks = async () => {
        try {
            const res = await axios.get('http://localhost:5000/api/tasks?limit=100');
            setTasks(res.data.data || []);
        } catch (err) {
            console.error(err);
        }
    };

    useEffect(() => {
        setIsMounted(true);
        fetchTasks();

        socket.on('taskCreated', (newTask: Task) => {
            setTasks(prev => [newTask, ...prev]);
        });

        socket.on('taskUpdated', (updatedTask: Task) => {
            // Optimistic UI might have already updated, but this ensures sync
            setTasks(prev => prev.map(t => t.id === updatedTask.id ? updatedTask : t));
        });

        socket.on('taskDeleted', (deletedId: number) => {
            setTasks(prev => prev.filter(t => t.id !== Number(deletedId)));
        });

        return () => {
            socket.off('taskCreated');
            socket.off('taskUpdated');
            socket.off('taskDeleted');
        };
    }, [refreshTrigger]);

    const onDragEnd = async (result: DropResult) => {
        const { source, destination, draggableId } = result;

        if (!destination) return;

        if (
            source.droppableId === destination.droppableId &&
            source.index === destination.index
        ) {
            return;
        }

        // Optimistic Update
        const updatedTasks = [...tasks];
        const taskIndex = updatedTasks.findIndex(t => t.id.toString() === draggableId);
        if (taskIndex === -1) return;

        // Create new array to avoid mutation
        const newStatus = destination.droppableId as Task['status'];
        const newTasks = tasks.map(t =>
            t.id.toString() === draggableId ? { ...t, status: newStatus } : t
        );

        setTasks(newTasks);

        // API Call
        try {
            await axios.put(`http://localhost:5000/api/tasks/${draggableId}`, {
                status: newStatus
            });
        } catch (err) {
            console.error('Failed to update task status', err);
            fetchTasks(); // Revert on failure
        }
    };

    const handleDelete = async (id: number) => {
        if (!confirm('Delete this task?')) return;
        try {
            await axios.delete(`http://localhost:5000/api/tasks/${id}`);
        } catch (err) {
            console.error(err);
        }
    };

    if (!isMounted) return null;

    const getTasksByStatus = (status: string) => {
        return tasks.filter(task => task.status === status);
    };

    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <div className="flex h-full gap-8 overflow-x-auto pb-4">
                {Object.entries(columns).map(([columnId, columnTitle]) => (
                    <div key={columnId} className={`flex h-full min-w-[320px] flex-col rounded-xl border p-4 shadow-sm ${columnColors[columnId as keyof typeof columnColors] || 'bg-gray-50'}`}>
                        <h2 className="mb-4 flex items-center justify-between text-sm font-bold text-gray-700 uppercase tracking-wide">
                            <span>{columnTitle}</span>
                            <span className="flex h-6 w-6 items-center justify-center rounded-full bg-white text-xs font-bold text-gray-600 shadow-sm border border-gray-100">
                                {getTasksByStatus(columnId).length}
                            </span>
                        </h2>
                        <Droppable droppableId={columnId}>
                            {(provided) => (
                                <div
                                    {...provided.droppableProps}
                                    ref={provided.innerRef}
                                    className="flex-1 overflow-y-auto px-1 min-h-[150px]"
                                >
                                    {getTasksByStatus(columnId).map((task, index) => (
                                        <TaskCard
                                            key={task.id}
                                            task={task}
                                            index={index}
                                            onDelete={handleDelete}
                                            canDelete={user?.role === 'admin' || user?.role === 'manager'}
                                        />
                                    ))}
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                    </div>
                ))}
            </div>
        </DragDropContext>
    );
}
