"use client";
import React, { useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import Board from '@/components/Board';
import { Button } from '@/components/ui/Button';
import CreateTaskModal from '@/components/CreateTaskModal';
import { LogOut, Plus, Layout, Bell, Settings } from 'lucide-react';

export default function DashboardPage() {
    const { user, logout } = useAuth();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [refreshTrigger, setRefreshTrigger] = useState(0);

    if (!user) return null;

    return (
        <div className="min-h-screen flex bg-gray-50 font-sans text-gray-900">
            {/* Sidebar */}
            <aside className="fixed left-0 top-0 z-40 h-screen w-64 border-r border-gray-200 bg-white">
                <div className="flex h-16 items-center gap-2 border-b border-gray-100 px-6">
                    <div className="h-8 w-8 rounded-lg bg-primary-600 flex items-center justify-center text-white font-bold text-lg">
                        T
                    </div>
                    <span className="text-lg font-bold text-gray-800">TaskFlow</span>
                </div>

                <div className="p-4 space-y-1">
                    <button className="flex w-full items-center gap-3 rounded-lg bg-primary-50 px-4 py-2 text-sm font-medium text-primary-700">
                        <Layout size={20} />
                        Board
                    </button>
                    <button className="flex w-full items-center gap-3 rounded-lg px-4 py-2 text-sm font-medium text-gray-600 hover:bg-gray-50 transition-colors">
                        <Bell size={20} />
                        Notifications
                    </button>
                    <button className="flex w-full items-center gap-3 rounded-lg px-4 py-2 text-sm font-medium text-gray-600 hover:bg-gray-50 transition-colors">
                        <Settings size={20} />
                        Settings
                    </button>
                </div>

                <div className="absolute bottom-4 left-0 w-full px-4 text-center">
                    <div className="mb-4 rounded-xl bg-gray-50 p-4">
                        <div className="mb-2 text-sm font-semibold text-gray-900">{user.username}</div>
                        <div className="text-xs text-gray-500 uppercase tracking-wide font-medium">{user.role}</div>
                    </div>
                    <Button variant="outline" onClick={logout} className="w-full justify-start text-red-500 hover:bg-red-50 hover:text-red-600 border-red-100 hover:border-red-200">
                        <LogOut size={18} className="mr-2" />
                        Sign Out
                    </Button>
                </div>
            </aside>

            {/* Main Content */}
            <main className="ml-64 flex-1 flex flex-col min-h-screen">
                <header className="sticky top-0 z-30 flex h-16 items-center justify-between border-b border-gray-200 bg-white/80 backdrop-blur-md px-8 py-4">
                    <div>
                        <h1 className="text-2xl font-bold text-gray-900">Project Board</h1>
                    </div>
                    <div className="flex items-center gap-4">
                        <Button onClick={() => setIsModalOpen(true)} className="shadow-lg shadow-primary-500/20">
                            <Plus size={18} className="mr-2" />
                            New Task
                        </Button>
                    </div>
                </header>

                <div className="flex-1 overflow-x-auto p-8">
                    <Board refreshTrigger={refreshTrigger} />
                </div>
            </main>

            <CreateTaskModal
                isOpen={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                onSuccess={() => setRefreshTrigger(prev => prev + 1)}
            />
        </div>
    );
}
